.move-free {
  @include page-grid;

  &__title {
    grid-column: 1 / -1;



    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: 1 / 5;
    }

  }

  &__description {
    margin: 0;
    grid-column: 1 / -1;
    font-size: 23px;
    font-weight: 500;
    line-height: 32.2px;


    @include on-tablet {
      grid-column: 4 / 7 ;
    }

    @include on-desktop {
      grid-column: 6 / 12;
    }
  }
}
